<template>
   <div>
      <!-- quitamos la clase bg-light para que sea transparente-->
      <!-- fixed top fija el navegador y hace que cuando se despliegue no se baje el menu de abajo-->
  <b-navbar toggleable="md" type="dark" variant="info" class="fuente fixed-top" id="idNavBar">
    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <router-link :to="'/quiensoy'">
        <span class="fuenteSkill">Juanma Caballero Rodr√≠guez - <strong>frontend Developer</strong></span>
      </router-link>
      <b-collapse is-nav  id="nav_collapse">
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto mt-2 mt-md-0">
          <b-nav-item
            v-for="route in routes"
            :key="route.indice"
            :label="route.tipo"
            :value="route" right>
              <router-link :to="route" class="nav-link" >
                  {{ route.name }}
              </router-link>
          </b-nav-item>
        </b-navbar-nav>
      </b-collapse>
</b-navbar>

  </div>
</template>

<script>
export default {
  name: 'Cabecera',
  data () {
    return {
      routes: []
    }
  },
  created () {
    for (var i in this.$router.options.routes) {
      if (!this.$router.options.routes.hasOwnProperty(i)) {
        continue
      }
      var route = this.$router.options.routes[i]
      if (route.hasOwnProperty('name')) {
        this.routes.push(route)
      }
    }
  }
}

</script>

