import Vue from 'vue'
import Router from 'vue-router'
import quiensoy from '@/components/quiensoy'
import quesehacer from '@/components/quesehacer'
import miexperiencia from '@/components/miexperiencia'
import contacto from '@/components/contacto'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/quiensoy'
    },
    {
      path: '/quiensoy',
      name: 'Qui√©n Soy',
      component: quiensoy
    },
    {
      path: '/quesehacer',
      name: 'Qu√© Se hacer',
      component: quesehacer
    },
    {
      path: '/miexperiencia',
      name: 'Mi Experiencia',
      component: miexperiencia
    },
    {
      path: '/contacto',
      name: 'Contacto',
      component: contacto
    } /*
    PENDIENTE
    ,
    {
      path: '*', component: NotFoundComponent
     } */
  ],
  // linkActiveClass: "active",// esto si queremos a√±adir a cada elemento activo la clase active
  mode: 'history'// esto es para quitar el famoso '#!' de nuestra URL.
})



